import React, { useEffect } from 'react';
import { useMaxBridgeContext } from '../providers/MaxBridgeProvider';
import { LoadingSpinner } from '../components/ui/StyledComponents.tsx';

export const MaxDemo: React.FC = () => {
  const {
    isMaxApp,
    isReady,
    user,
    showBackButton,
    onBackButtonClick,
    hapticFeedback,
    shareContent,
    requestPhone,
  } = useMaxBridgeContext();

  useEffect(() => {
    if (isMaxApp) {
      //   
      showBackButton(true);

      //   
      onBackButtonClick(() => {
        hapticFeedback('impact', { style: 'light' });
        console.log('Back button pressed');
      });
    }

    return () => {
      if (isMaxApp) {
        showBackButton(false);
      }
    };
  }, [isMaxApp, showBackButton, onBackButtonClick, hapticFeedback]);

  if (!isMaxApp) {
    return (
      <div className="container">
        <h1>   MAX</h1>
        <p>     MAX </p>
      </div>
    );
  }

  if (!isReady) {
    return (
      <LoadingSpinner
        appearance="primary"
        size={20}
      />
    );
  }

  return (
    <div className="container">
      <h1>MAX Bridge Demo</h1>

      {user && (
        <div className="user-info">
          <h2>, {user.first_name}!</h2>
          <p>Username: @{user.username}</p>
          <p>Language: {user.language_code}</p>
        </div>
      )}

      <div className="actions">
        <button onClick={() => hapticFeedback('impact', { style: 'medium' })}
                className="btn">
          
        </button>

        <button
          onClick={() => shareContent(' ', 'https://example.com')}
          className="btn"
        >
          
        </button>

        <button
          onClick={async () => {
            try {
              const phone = await requestPhone();
              console.log('Phone:', phone);
            } catch (error) {
              console.error('Error requesting phone:', error);
            }
          }}
          className="btn"
        >
           
        </button>
      </div>
    </div>
  );
};
